<template>
  <div class="boxDiv">
    <el-card>
      <div slot="header" class="clearfix">
        <CommonMissStationHeader></CommonMissStationHeader>
      </div>

      <el-row :gutter="20">
        <el-col :span="14">
          <div class="clearfix">
            <span>应包装数量 : {{ Package_Qty }}</span>
            <span class="ml20">
              已包装数量 : {{ form.product_arr.length }}/
              {{ Package_Qty }}</span
            >
            <span class="ml20" v-if="pack_idFlag">
              包装编码 : {{ form.pack_id }}</span
            >
          </div>

          <el-form
            style="padding: 0 60px 0 20px; width: 90%; margin: 20px 0 40px"
            ref="formRef"
            :model="form"
            :rules="formRules"
            label-width="none"
          >
            <el-form-item v-show="pack_idFlag" prop="pack_id" label="包装编码">
              <el-input
                v-model="form.pack_id"
                ref="firstInput"
                @input="pack_id_change"
                clearable
              ></el-input>
            </el-form-item>

            <el-form-item
              :prop="firstFinishedProductCode"
              label="产品编码 1."
              v-show="!pack_idFlag && finishedProductCode0Flag"
            >
              <el-input
                ref="finishedProductCode0"
                v-model="form.finished_product_code"
                clearable
                @input="
                  nextFocus(
                    'finishedProductCode1',
                    1,
                    form.finished_product_code
                  )
                "
              ></el-input>
            </el-form-item>

            <div
              v-for="(item, index) in form.response_datas"
              :key="index + ' onlyItem'"
            >
              <!-- :rules="[
              {
                min: Product_length,
                max: Product_length,
                message: '编码长度为' + Product_length + '个字节。',
                trigger: 'blur',
              },
            ]" -->

              <!-- {{ "finishedProductCode" + (index + 1) }} -->
              <!-- v-show="!pack_idFlag" -->

              <el-form-item
                :prop="'response_datas.' + index + '.finished_product_code'"
                :label="'产品编码 ' + (index + 2) + '.'"
                v-show="form.product_arr.length - 1 == index && !pack_idFlag"
              >
                <el-input
                  :ref="'finishedProductCode' + (index + 1)"
                  v-model="item.finished_product_code"
                  clearable
                  @input="
                    nextFocus(
                      'finishedProductCode' + (index + 2),
                      index + 2,
                      item.finished_product_code
                    )
                  "
                ></el-input>
              </el-form-item>
            </div>
          </el-form>

          <div class="mt20">
            <el-button @click="resetForm">重 置</el-button>
            <el-button
              style="margin-left: 60px"
              type="primary"
              @click="dialogSave"
              >确 定</el-button
            >
          </div>
        </el-col>

        <el-col :span="10">
          <div ref="PieChart" class="chart chartClass"></div>
        </el-col>
      </el-row>
    </el-card>

    <div class="formDiv mt20">
      <el-form :inline="true" class="demo-form-inline">
        <!-- 查询条件 -->
        <el-form-item label="时间">
          <el-date-picker
            v-model="listQuery.daterangeArr"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd"
            format="yyyy-MM-dd"
            @change="daterange_change"
          >
          </el-date-picker>
        </el-form-item>

        <!-- 查询按钮 -->
        <el-form-item style="margin-left: 20px !important">
          <el-button type="primary" size="medium" @click="searchBtn"
            >查 询</el-button
          >
          <el-button size="medium" @click="resetTable()">重 置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- 表格 -->
    <el-card class="box-card mt20">
      <div slot="header" class="clearfix">
        <span>成品列表</span>
      </div>
      <div class="">
        <ELTable
          :tableCol="tableCol"
          :tableData="tableData"
          :listQuery="listQuery"
          :ispagination="true"
          @currentChange="currentChange"
          @sizeChange="sizeChange"
        >
        </ELTable>
      </div>
    </el-card>
  </div>
</template>

<script>
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";
import CommonMissStationHeader from "@/components/CommonMissStationHeader";
import ELTable from "@/components/formTablePage/ElTable";
import { formatDate } from "@/assets/libs/unit";
import * as echarts from "echarts";

export default {
  name: "MissStation",

  components: {
    CommonMissStationHeader,
    ELTable,
  },
  created() {
    window.addEventListener("keydown", this.handleKeyDown, true); //开启监听键盘按下事件
  },
  computed: {
    ...mapState(["ProductionPlanCode", "PersonCodeNameList"]),
  },
  data() {
    return {
      finishedProductCode0Flag: true,
      pack_idFlag: false,
      confirmFlag: false,
      myChart: null,
      seriesData: [],
      titleTotal: 0,
      timeout: "",
      // 产品过站弹出层
      form: {
        pack_id: "",
        product_arr: [],
        response_datas: [],
      },
      formBase: {
        pack_id: "",
        product_arr: [],
        response_datas: [],
      },
      formRules: {
        // count: [
        //   { required: true, message: "请输入数量", trigger: "blur" },
        //   { type: "number", message: "数量必须为数值", trigger: "blur" },
        // ],
        pack_id: [
          { required: true, message: "请输入包装编码", trigger: "blur" },
        ],
      },

      listQuery: {
        daterangeArr: [
          formatDate(new Date(), "yyyy-MM-dd 00:00:00"),
          formatDate(new Date(), "yyyy-MM-dd 23:59:59"),
        ],
        start_time: formatDate(new Date(), "yyyy-MM-dd 00:00:00"),
        end_time: formatDate(new Date(), "yyyy-MM-dd 23:59:59"),
        product_plan_code: "",
        // enter_time: "",
        // enter_user: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      listQueryBase: {
        daterangeArr: [
          formatDate(new Date(), "yyyy-MM-dd 00:00:00"),
          formatDate(new Date(), "yyyy-MM-dd 23:59:59"),
        ],
        start_time: formatDate(new Date(), "yyyy-MM-dd 00:00:00"),
        end_time: formatDate(new Date(), "yyyy-MM-dd 23:59:59"),
        product_plan_code: "",
        // enter_time: "",
        // enter_user: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      Package_Qty: 0,
      Shipping_SN_length: 0,
      Product_length: 0,

      // 表格列
      tableCol: [
        {
          label: "包装编码",
          field: "pack_id",
          align: "center",
        },
        {
          label: "产品编码",
          field: "product_id",
          align: "center",
        },
        {
          label: "产品名称",
          field: "product_name",
          align: "center",
        },
        {
          label: "数量",
          field: "product_count",
          align: "center",
        },
        {
          label: "操作人员",
          width:"200px",
          field: "user_name",
          align: "center",
        },
        {
          label: "状态",
          width:"200px",
          field: "status",
          align: "center",
          colType: "tag",
          tagRender: (row) => {
            let params = {
              tagType: "",
              tagText: row.status,
            };
            if (row.status == "PASS") {
              params.tagType = "";
              params.tagText = "已包装";
            } else {
              params.tagType = "warning";
            }
            return params;
          },
        },
        {
          label: "时间",
          field: "enter_time",
          align: "center",
        },
      ],
      tableData: [],
    };
  },
  methods: {
    ...mapActions(["getLogisticProductPlanDeal", "getLogisticPersonCodeName"]),

    daterange_change() {
      if (this.listQuery.daterangeArr.length > 0) {
        this.listQuery.start_time =
          this.listQuery.daterangeArr[0] + " 00:00:00";
        this.listQuery.end_time = this.listQuery.daterangeArr[1] + " 23:59:59";
      }
    },
    // 查询
    searchBtn() {
      this.listQuery.page = 1;
      this.getLogisticFinishedProductStorage(this.listQuery);
    },
    // 表格重置
    resetTable() {
      this.listQuery = JSON.parse(JSON.stringify(this.listQueryBase));
      this.getLogisticFinishedProductStorage(this.listQuery);
    },
    // 打印参数列表 查询
    getLogisticOperationSystem(params) {
      baseManagement.getLogisticOperationSystem(params).then((res) => {
        console.log("打印参数列表 查询", res);
        if (res.code == 0) {
          this.Package_Qty = res.data.data[0].Package_Qty;
          this.Shipping_SN_length = res.data.data[0].Shipping_SN_length;
          this.Product_length = res.data.data[0].Product_length;
          this.inputInit();

          console.log("this.Package_Qty ", this.Package_Qty);
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },
    pack_id_change() {
      if (this.timeout) {
        clearTimeout(this.timeout);
      }
      this.timeout = setTimeout(() => {
        if (this.form.pack_id.length != this.Shipping_SN_length) {
          this.$message.warning(
            "包装盒编码长度为" + this.Shipping_SN_length + "个字节。"
          );
          return false;
        }
        this.pack_idFlag = true;

        this.$nextTick(() => {
          this.$refs.firstInput.focus();
        });
      }, 500);
    },
    nextFocus(nextRef, index, matter_code) {
      if (this.timeout) {
        clearTimeout(this.timeout);
      }
      this.timeout = setTimeout(() => {
        // console.log(nextRef);
        // console.log(index);
        // console.log(matter_code);
        // console.log(matter_code.length);
        if (nextRef == "finishedProductCode1") {
          this.finishedProductCode0Flag = false;
        }

        if (matter_code.length != this.Product_length) {
          console.log("nextRef nextRef nextRef", nextRef);
          if (nextRef == "finishedProductCode1") {
            this.finishedProductCode0Flag = true;
          }
          this.$message.warning(
            "产品编码长度为" + this.Product_length + "个字节。"
          );
          return false;
        }

        for (let i in this.form.product_arr) {
          if (this.form.product_arr[i] == matter_code) {
            if (nextRef == "finishedProductCode1") {
              this.finishedProductCode0Flag = true;
            }
            this.$message.warning("已包装过此产品！");
            return false;
          }
        }

        baseManagement
          .getLogisticPackCheckFinishedProduct({
            finished_product_code: matter_code,
          })
          .then((res) => {
            console.log("包装工站的产品检验产品是否合格接口 新增", res);
            if (res.code == 0) {
              this.form.product_arr.push(matter_code);

              if (index < this.form.response_datas.length + 1) {
                this.$nextTick(() => {
                  this.$refs[nextRef][0].focus();
                });
              } else {
                this.pack_idFlag = true;
                this.$nextTick(() => {
                  this.$refs.firstInput.focus();
                });
              }
            } else {
              // this.resetForm();
              if (nextRef == "finishedProductCode1") {
                this.finishedProductCode0Flag = true;
              }
              this.$nextTick(() => {
                this.$refs.finishedProductCode0.focus();
              });
              this.$message({
                message: res.message,
                type: "error",
              });
            }
          });
      }, 500);
    },

    // 产品过站 新增
    postLogisticFinishedProductStorage(params) {
      baseManagement.postLogisticFinishedProductStorage(params).then((res) => {
        console.log("产品过站 新增", res);
        if (res.code == 0) {
          this.resetForm();

          // this.$refs.firstInput.focus();
          // 生产计划-成品列表
          this.getLogisticFinishedProductStorage(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },
    inputInit() {
      for (let index = 1; index < this.Package_Qty; index++) {
        this.form.response_datas.push({
          pack_id: "",
          finished_product_code: "",
        });
      }
    },
    // 重置按钮
    resetForm() {
      this.finishedProductCode0Flag = true;
      this.pack_idFlag = false;
      this.form = JSON.parse(JSON.stringify(this.formBase));
      this.inputInit();
      this.$nextTick(() => {
        this.$refs.finishedProductCode0.focus();
      });
    },

    // 确认按钮
    dialogSave() {
      console.log(
        "this.ProductionPlanCode this.ProductionPlanCode",
        this.ProductionPlanCode
      );

      if (!this.ProductionPlanCode) {
        this.$message.warning("没有进行中的生产计划！");
        return false;
      }

      if (
        this.form.product_arr.length < this.Package_Qty &&
        !this.confirmFlag
      ) {
        this.$confirm("未包装满应包装数量，确认完成包装么", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
          //   beforeClose(action, instance, done){
          //   if(action == 'confirm'){
          //     instance.$refs['confirm'].$el.onclick = a()
          //     function a(e){
          //       e = e || window.event;
          //       if(e.detail != 0){
          //         done()
          //       }
          //     }
          //   }else{
          //     done()
          //   }
          // }
        }).then(() => {
          // this.postLogisticFinishedProductStorage(params);
          this.pack_idFlag = true;
          this.confirmFlag = true;

          this.$nextTick(() => {
            this.$refs.firstInput.focus();
          });
        });
      } else {
        this.$refs.formRef.validate((valid) => {
          if (!valid) {
            this.$message({
              message: "请修改正确的数据格式！",
              type: "warning",
            });
            return;
          } else {
            if (this.form.product_arr.length <= 0) {
              this.$message({
                message: "产品格式不正确或产品为空，请检查！",
                type: "warning",
              });
              return false;
            }
            if (this.form.pack_id.length != this.Shipping_SN_length) {
              this.$message.warning(
                "包装盒编码长度为" + this.Shipping_SN_length + "个字节。"
              );
              return false;
            }
            this.pack_idFlag = false;
            this.confirmFlag = false;

            //验证通过调用保存接口
            let params = {
              pack_id: this.form.pack_id,
              product_plan_code: this.ProductionPlanCode,
              response_datas: [],
            };

            this.form.product_arr.forEach((item) => {
              let obj = {
                pack_id: this.form.pack_id,
                finished_product_code: item,
              };

              params.response_datas.push(obj);
            });

            console.log("产品过站", params);

            this.postLogisticFinishedProductStorage(params);
          }
        });
      }
    },
    handleKeyDown(e) {
      let key = null;
      if (window.event === undefined) {
        key = e.keyCode;
      } else {
        key = window.event.keyCode;
      }
      if (key === 13) {
        if (this.confirmFlag) {
          this.dialogSave();
        } else if (this.form.product_arr.length == this.Package_Qty) {
          this.dialogSave();
        }
      }
    },
    // 生产计划-成品列表
    getLogisticFinishedProductStorage(params) {
      params.product_plan_code = this.ProductionPlanCode;
      baseManagement.getLogisticFinishedProductStorage(params).then((res) => {
        console.log("成品列表 查询", res);
        if (res.code == 0) {
          let tableData = res.data.data;
          this.seriesData = [{ value: res.data.product_num, name: "包装总数" }];
          this.titleTotal = res.data.product_num;
          this.chartInit();
          tableData.forEach((tableDataItem) => {
            if (tableDataItem.response_datas) {
              tableDataItem.product_count = tableDataItem.response_datas.length;
            } else {
              tableDataItem.product_count = 0;
            }

            this.PersonCodeNameList.forEach((CodeNameListItem) => {
              if (tableDataItem.enter_user == CodeNameListItem.user_code) {
                tableDataItem.user_name = CodeNameListItem.user_name;
              }
            });
          });
          this.tableData = tableData;
          this.listQuery.total = res.data.total;
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },
    // 当前页面改变时
    currentChange(val) {
      this.listQuery.page = val;
      this.getLogisticFinishedProductStorage(this.listQuery);
    },
    // 每页条数发生改变时
    sizeChange(val) {
      this.listQuery.page = 1;
      this.listQuery.page_size = val;
      this.getLogisticFinishedProductStorage(this.listQuery);
    },

    chartInit() {
      let _this = this;
      if (
        this.myChart != null &&
        this.myChart != "" &&
        this.myChart != undefined
      ) {
        this.myChart.dispose();
      }
      this.myChart = echarts.init(this.$refs.PieChart);

      // this.myChart.showLoading();

      //   myChart.showLoading();
      let option = {};
      // this.noneFlag = true;
      this.noneFlag = false;
      for (let i in this.seriesData) {
        if (this.seriesData[i].value > 0) {
          this.noneFlag = true;
        }
      }

      if (this.noneFlag) {
        option = {
          title: {
            text: "总计:" + this.titleTotal,
            top: "5%",
            right: "8%",
            textStyle: {
              // color: "#fff",
              fontSize: "16",
            },
          },
          color: ["#5470c6", "#FAC858"],
          tooltip: {
            trigger: "item",
            formatter: (params) => {
              return (
                params.name +
                ":  " +
                params.value +
                " " +
                ((params.value / this.titleTotal) * 100).toFixed(2) +
                "%"
              );
            },
          },
          legend: {
            type: "scroll",
            orient: "vertical",
            top: "center",
            right: "3%",
            textStyle: {
              padding: 5,
            },

            // formatter: function (params) {
            //   console.log(12321321, params);
            //   let value = 0;
            //   for (let i in _this.seriesData) {
            //     if (params == _this.seriesData[i].name) {
            //       value = _this.seriesData[i].value;
            //     }
            //   }
            //   return `${params} - ${value}`;
            // },
          },
          series: [
            {
              // name: this.seriesName,
              // name: '公司所用工时',
              type: "pie",
              radius: ["45%", "75%"],
              center: ["50%", "50%"],
              avoidLabelOverlap: false,
              label: {
                show: false,
                position: "center",
              },
              // itemStyle: {
              //   normal: {
              //     borderColor: "#fff",
              //     borderWidth: 2,
              //   },
              // },
              emphasis: {
                label: {
                  show: true,
                  // fontSize: "20",
                  // fontWeight: "bold",
                },
              },
              label: {
                // show:noneFlag,
                formatter: " {b|{b}：}{c}  {per|{d}%}  ",
                // backgroundColor: '#F6F8FC',
                // borderColor: '#8C8D8E',
                // borderWidth: 1,
                borderRadius: 4,

                rich: {
                  // b: {
                  //     color: '#4C5058',
                  //     fontSize: 14,
                  //     fontWeight: 'bold',
                  //     lineHeight: 33
                  // },
                  per: {
                    // color: '#fff',
                    // backgroundColor: '#4C5058',
                    // padding: [3, 4],
                    borderRadius: 4,
                  },
                },
              },
              labelLine: {
                // show: false,
              },
              data: this.seriesData,
            },
          ],
        };
      } else {
        //  this.myChart.hideLoading();
        option = {
          title: {
            text: "无数据",
            top: "center",
            left: "center",
          },
          series: [],
        };
      }
      // this.myChart.hideLoading();
      this.myChart.setOption(option);
      this.myChart.on("click", function (params) {
        _this.$emit("pieChartClick", params);
      });

      window.addEventListener("resize", function () {
        _this.myChart.resize();
      });
    },
  },
  mounted() {
    // 打印参数列表 查询
    this.getLogisticOperationSystem(this.listQuery);
    // 进行中的生产计划接口：
    this.getLogisticProductPlanDeal();
    // 人员内码和人员名称 查询
    this.getLogisticPersonCodeName();
    // 生产计划-成品列表
    this.getLogisticFinishedProductStorage(this.listQuery);

    this.$nextTick(() => {
      this.$refs.finishedProductCode0.focus();
    });
  },
};
</script>

<style lang="scss" scoped>
.boxDiv span {
  font-size: 14px;
}
.el-card .el-form-item {
  // margin: 0;
  /deep/ .el-form-item__label {
    font-size: 24px !important;
  }
  /deep/ .el-input__inner {
    font-size: 24px !important;
  }
  /deep/ .el-form-item__error {
    font-size: 16px !important;
  }
}
.chartClass {
  width: 100%;
  height: 250px;
}
</style>